.nav-bar{
    background: #fff;
    height: 40px;
    padding-top: 10px;
    -webkit-box-shadow: 0px 3px 4px 0px rgba(50, 50, 50, 0.73);
-moz-box-shadow:    0px 3px 4px 0px rgba(50, 50, 50, 0.73);
box-shadow:         0px 3px 4px 0px rgba(50, 50, 50, 0.73);
}
.nav-bar ul{
    list-style: none;
    display: flex;
    justify-content: space-around;
    
}

.nav-bar ul li a{
    text-decoration: none;
    text-transform: uppercase;
    font-weight: bold;
    color: #000;
    text-align: center;
}

.dropdown ul li .dropdown-content a{
    color: #000;
}

.nav-bar ul li a:hover{
    color: #000
}

.dropdown-content a{
    color: #000;
}

.link-text{
    color: #000;
}


.dropbtn {
    color: white;
    margin-top: 10px;
    font-size: 16px;
    border: none;
}

.dropd

  /* The container <div> - needed to position the dropdown content */
.dropdown {
    position: relative;
    display: inline-block;
}
  
  /* Dropdown Content (Hidden by Default) */
.dropdown-content {
    display: none;
    position: absolute;
    background-color: #f1f1f1;
    min-width: 160px;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    z-index: 1;
}
  
  /* Links inside the dropdown */
.dropdown-content a {
    color: black;
    padding: 12px 16px;
    text-decoration: none;
    display: block;
}
  
  /* Change color of dropdown links on hover */
.dropdown-content a:hover {
      background-color: #ddd;
}
  
  /* Show the dropdown menu on hover */
.dropdown:hover .dropdown-content {display: block;}
  
  /* Change the background color of the dropdown button when the dropdown content is shown */
  
.dropdown:hover .dropbtn {
      color: green;
    }