.cards-extern .cards1{
    display: flex;
    justify-content: space-around;
}

.cards-extern h2 {
    margin-left: 15px;
    font-size: 45px;
    font-weight: bold;
}

.cardProducts{
    width: 330px;
    height: 450px;
    border: 1px solid rgba(0, 0, 0, 0.199) ;
    border-radius: 5px;
    margin: 10px;
    -webkit-box-shadow: 2px 2px 9px 0px rgba(50, 50, 50, 0.32);
    -moz-box-shadow:    2px 2px 9px 0px rgba(50, 50, 50, 0.32);
    box-shadow:         2px 2px 9px 0px rgba(50, 50, 50, 0.32);
}

.card-header-products{
    height: 50px;
    background-color: #05c949;
    border-radius: 5px 5px 0 0;
    
}


.cardProducts h5{
    margin-top: 10px;
    text-align: center;
    font-size: 20px;
    color: #000;
}

.cardProducts p {
    color: #000;
}

.card-header-products p {
    color: #ffff;
    font-weight: bold;
    font-size: 20px;
    text-align: center;
    padding-top: 10px;
}
.cardProducts img{
    display: block;
    width: 240px;
    height: 170px;
    margin-left: auto;
    margin-right: auto;
}

.cardProducts .short-description{
    text-transform: lowercase;
    font-size: 16px;
    margin-left: 5px;
}

